.logos {
    margin: 150px 0;
    text-align: center;
}

.logos .cinema:hover {
    -webkit-transform: translate(0, -50px);
        -ms-transform: translate(0, -50px);
            transform: translate(0, -50px);
    -webkit-filter: drop-shadow(0 100px 0 #341f97);
            filter: drop-shadow(0 100px 0 #341f97);
}

.logos .netflix:hover {
    -webkit-transform: translate(0, -50px);
        -ms-transform: translate(0, -50px);
            transform: translate(0, -50px);
    -webkit-filter: drop-shadow(0 100px 0 #ee5253);
            filter: drop-shadow(0 100px 0 #ee5253);
}

.logos .hbo-go:hover {
    -webkit-transform: translate(0, -50px);
        -ms-transform: translate(0, -50px);
            transform: translate(0, -50px);
    -webkit-filter: drop-shadow(0 100px 0 #2e86de);
            filter: drop-shadow(0 100px 0 #2e86de);
}

.logos a {
    color: #2e86de;
}


@media screen and (max-width: 991px) {
    .logos .cinema:hover, .logos .netflix:hover, .logos .hbo-go:hover {
        -webkit-transform: initial;
            -ms-transform: initial;
                transform: initial;
        -webkit-filter: initial;
                filter: initial;
    }
}


